import React from 'react'
import { Text } from 'uikit'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import BigNumber from 'bignumber.js'
import useAllEarnings from 'hooks/useAllEarnings'
import { FarmType } from 'config/constants/types'

interface CardValueProps {
  necroPrice?: BigNumber,
  farmType: FarmType
}

const CakeHarvestBalance: React.FC<CardValueProps> = ({ necroPrice, farmType }) => {
  const { account } = useWallet()
  const allEarnings = useAllEarnings(farmType)
  const earningsSum = allEarnings.reduce((accum, earning) => {
    return accum + new BigNumber(earning).div(new BigNumber(10).pow(18)).toNumber()
  }, 0)
  const allvalue = earningsSum.toFixed(4)

  const allEarningsX = necroPrice.multipliedBy(earningsSum).toFixed(2)
  if (!account) {
    return (
      <Text color="textDisabled" style={{ lineHeight: '60px' }}>
        Locked
      </Text>
    )
  }

  return (
    <Text bold fontSize="24px" style={{ lineHeight: '60px' }} color="secondary">
      {' '}
      {` ${allvalue} ( ${allEarningsX} $ )`}{' '}
    </Text>
  )
}

export default CakeHarvestBalance
