import { useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import multicall from 'utils/multicall'
import { getMasterChefAddress } from 'utils/addressHelpers'
import farms from 'config/constants'
import { FarmType } from 'config/constants/types'
import { getChefAbi } from './useContract'
import useRefresh from './useRefresh'

const useAllEarnings = (type: FarmType) => {
  const [balances, setBalance] = useState([])
  const { account }: { account: string } = useWallet()
  const { fastRefresh } = useRefresh()

  useEffect(() => {
    const fetchAllBalances = async () => {
      const calls = farms
        .filter((x) => x.type === type)
        .map((farm) => ({
          address: getMasterChefAddress(farm.type),
          name: 'pendingReward',
          params: [farm.contractPid, account],
        }))
        
      try {
        const res = await multicall(getChefAbi(type) as any, calls)

        setBalance(res)
      } catch {
        console.error('IGNORE FOR NOW')
      }
    }

    if (account) {
      fetchAllBalances()
    }
  }, [account, fastRefresh, type])

  return balances
}

export default useAllEarnings
